'use strict';

function Post(conn) {
  this._conn = conn;
}

Post.prototype = {
  list: function list(options) {
    return this._conn.get('posts/', options);
  },
  retrieve: function retrieve(slug, options) {
    return this._conn.get('posts/' + slug + '/', options);
  },
  search: function search(query, options) {
    var options = options || {};
    options.query = query || '';
    return this._conn.get('search/', options);
  }
};
module.exports = Post;